import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
import Home from "../views/Home";
import admin from './admin'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        redirect: "/index",
        component:Home,
        children: [
            {
                path: 'index',
                name: 'index',
                component: Index
            },
            {
                path: 'category/:name',
                name:'categories',
                component:Index,
                meta:{
                    title:''
                }
              },
            {
                path: 'archive/:name',
                name:'archive',
                component:Index,
                meta:{
                    title:''
                }
            },
            {
                path: 'post/:id',
                name: 'post',
                component: () => import('../views/Post'),
                meta:{
                    title:''
                }
              },
            {
                path: 'tags/:name',
                name: 'tags',
                component: () => import('../views/Index')
            },
            {
                path: 'search/:name',
                name: 'search',
                component: () => import('../views/Index')
            },
            {
                path: 'about',
                name: 'about',
                component:()=>import('../views/About'),
                meta:{
                    title:''
                }
            },
            {
                path: 'friends',
                name: 'friends',
                component: () => import('../views/Friends')
              },

        ]
    },
    {
        path: '/login',
        name: 'login',
        component: () => import('../views/admin/Login')
      },
    {
        path: '/admin',
        name: 'admin',
        redirect:'/admin/home',
        component: () => import('@/views/admin/layout/index'),
        children:[
            ...admin
        ]
    },
    {
        path: '/404',
        name: 'notFound',
        component: () => import('../views/404'),
        meta:{
            title:"404"
        }
      },
    {
        path: '*',
        redirect: '/404'
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
