import request from '@/utils/request'
const qs =require("querystring")
export function fetchList(query) {
  return request({
    url: '/post/getAllList',
    method: 'get',
    params: query
  })
}
export function getCategoryApi(){
  return request({
    url: '/category/getList',
    method: 'get'
  })
}

export function addCategoryApi(data){
  return request({
    url: '/category/add',
    method: 'post',
    data
  })
}
export function updateCategoryApi(data){
  return request({
    url: '/category/update',
    method: 'put',
    data
  })
}

export function createArticleApi(data) {
  return request({
    url: '/post/create',
    method: 'post',
    data
  })
}
// 
export function getTopicApi(id) {
  return request({
    url: '/post/view',
    method: 'get',
    params: {
      id: id
    }
  })
}
export function getViewApi(id){
  return request({
    url: '/post/viewTopic',
    method: 'get',
    params: {
      id: id
    }
  })
}
export function deleteTopicApi(id){
  return request({
    url: '/post/delete',
    method: 'delete',
    params: {
      id: id
    }
  })
}
export function getUpdateArticleApi(articleId){
  return request({
    url: '/post/getUpdateData',
    method: 'get',
    params:{articleId}
  })
}

export function updateArticleApi(data) {
  return request({
    url: '/post/update',
    method: 'post',
    data
  })
}

export function updateStatus(data) {
  let qd = qs.stringify(data)
  return request({
    url: '/post/updateStatus',
    method: 'post',
    headers:{
      'Content-Type': 'application/x-www-form-urlencoded',
    },
    data:qd
  })
}

/**
 * 
 * @param query {{page:Number,size:Number,tab:String,param:String}}
 * @returns {*}
 */
export function getListApi(query){
  return request({
    url: '/post/list',
    method: 'get',
    params:query
  })
}
