import request from '../utils/request'

export function getBasicConfigApi(){
    return request({
        url:"/config/getBasicConfig",
        method:"get"
    })
}

export function getHeaderApi(){
    return request({
        url:"/post/getHeader",
        method:"get"
    })
}

export function getBillBoardShow(){
    return request({
        url:"/billboard/show",
        method:"get"
    })
}


export function getTagsAPi(){
    return request({
        url:"/tag/getList",
        method:"get"
    })
}
