<template>
  <div>
    <div class="article">

      <router-link class="post"
                   v-for="(item,index) in tableData" :key="index"
                   :to="{name:'post',params:{id:item.articleId}}" data-pjax-state="">
        <div class="mdui-card index-card mdui-hoverable" :style="!showCover?'height: 100px':''">
          <div v-show="item.top" class="top">
            <div></div>
            <!--<i class="mdui-icon material-icons">message</i>-->
            <div  style=" width:30px;height:20px;background-color: red"></div>
            <div  style=" width:30px;height:10px;background-color: yellow">
            </div>
            <div class="div3"></div>
            <div style="display: flex">
              <div id="div1"></div>
              <div id="div2"></div>
            </div>
          </div>
          <div class="mdui-card-media index-img-media" style="height: 100%;">
            <div v-show="showCover" class="index-img entered loaded"
                 :style="`background-image: url(${item.cover});`"></div>
            <div class="index-card-filter">
            </div>
            <div class="mdui-card-media-covered" :style="!showCover?'color: #000':''">
              <div class="mdui-card-primary index-primary">
                <div class="mdui-card-primary-title">
                  {{ item.title }}
                </div>
                <div class="mdui-card-primary-subtitle index-info">
                  {{ dayjs(item.createTime).format(timeFormat) }}
                </div>
                <div class="mdui-card-primary-subtitle index-subtitle">
                  <span style="margin-right: 10px" v-for="i in item.tags" :key="i">
                    #{{i}}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </router-link>
    </div>
    <div v-show="tableData.length<=0"
         class="mdui-card archive-card mdui-hoverable">
      <div class="archive-title">
        <p></p>
      </div>
    </div>
  </div>

</template>

<script>

export default {
  name: "TopicList",

  data() {
    return {}
  },
  props: {
    tableData: {
      type: Array,
      required: true
    },
    showCover: {
      type: Boolean,
      default: false,
      required: true
    },
    timeFormat: {
      type: String,
      required: true
    },
    total: {
      type: Number,
      default: 0
    }
  }
}
</script>

<style scoped>
.index-card {
  transform: scale(1);
}

.top {
  color: red;
  z-index: 1;
  position: absolute;
  top: 0;
  right: 10px;
  border: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;

  transition: all 0.2s;
}
.div3{
  width: 30px;
  height: 40px;
  background-color: red;
}
#div1 {
  width: 0;
  height: 0;
  border: 10px solid red;
  border-right: 5px solid transparent;
  border-bottom: 20px solid transparent;
}

#div2 {
  width: 0;
  height: 0;
  border: 10px solid red;
  border-left: 5px solid transparent;
  border-bottom: 20px solid transparent;
}


</style>
