<template>
  <div class="container context">
    <div>
      <div class="box">
        <el-carousel height="20px" style="line-height: 1" direction="vertical" indicator-position="none" arrow="never">
          
          <el-carousel-item v-for="item in billboard" :key="item.content" style="left: 20px;width: 80%">
            <span class="medium">{{ item.content }}</span>
          </el-carousel-item>
        </el-carousel>
      </div>
      <div class="index-container">
        <div class="mdui-col-md-8">
          <TopicList :table-data="tableData" :time-format="timeFormat" :show-cover="showCover">

          </TopicList>
        <Pagination :hidden="tableData.length<=query.size" :limit.sync="query.size"
        :page.sync="page" :total="total" @pagination="getList"></Pagination>
        </div>
        <div class="mdui-col-md-4">
          <CardBar></CardBar>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import CardBar from "../components/Card/CardBar";
import TopicList from "../components/TopicList";
import Pagination from "../components/Pagination";
import {getListApi} from "../api/article";
import {getBillBoardShow} from "../api";
import {mapGetters} from "vuex";

export default {
  name: "Index",
  components: {TopicList, CardBar,Pagination},
  data() {
    return {
      total:0,
      page:1,
      query: {
        page: 1,
        size: 10,
        tab: 'index',
        param: null
      },
      showCover:false,
      timeFormat:'YYYYMMDD',
      tableData: [],
      billboard: [{
        content: "1",
      }, {
        content: "2",
      }, {
        content: "3",
      },],
    }
  },
  computed: {
    ...mapGetters(['basicConfig'])
  },
  created() {
    this.page=parseInt(this.$route.query.p||"1")
  },
  mounted() {
    if (this.basicConfig){
      this.query.size=this.basicConfig.postPageSize
    }
    this.getList(this.page)
    this.getBillBoard()
  },
  methods: {
    getBillBoard(){
      getBillBoardShow().then(res=>{
        //console.log(res)
        this.billboard=res.data
      })
    },
    getList(page) {
      this.query.page=page
      this.query.tab = this.$route.name
      this.query.param = this.$route.params.name || null
      getListApi(this.query).then(res => {
       //console.log(res)
        this.showCover=this.basicConfig.showFeatureImage
        this.timeFormat=this.basicConfig.dateFormat
        this.tableData = res.data.records
        this.total=res.data.total
      })
    }
  },
}
</script>
<style scoped>
.box {
  margin: 0 8px !important;
  padding: 10px;
}

.index-container {
  padding-top: 20px !important;
}
</style>
