<template>
  <div class="body">
    <div class="background" :style="`background-image: url(${topicConfig.backgroundImage});`">
      <div class="index-filter"></div>
    </div>
    <Header></Header>
    <router-view :key="$route.path"></router-view>
    <back-top></back-top>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "../components/Layout/Header";
import {setConfig, getSession} from "../utils/SessionStorage";
import {getBasicConfigApi} from "../api";
import BackTop from "../components/BackTop";
import {mapGetters} from "vuex";
import mouse from "../utils/magic/mouse";
import Footer from "../components/Layout/Footer";

export default {
  name: "Home",
  components: {Footer, BackTop, Header},
  data() {
    return {
      isRouterAlive: true,
      billboard: [{
        content: "1",
      }, {
        content: "2",
      }, {
        content: "3",
      },],
    }
  },
  computed: {
    ...mapGetters(['topicConfig'])
  },
  created() {
    this.setConfig()
  },
  mounted() {
    switch (this.topicConfig.mouse) {
      case "none":
        break;
      case "peace":
        mouse.peace()
        break;
      case "love":
        mouse.love()
        break;
      case "fireworks":
        mouse.fireworks()
        break;
    }
  },
  methods: {
    getConfig() {
      getSession("user")
    },
    setConfig() {
      let config = getSession("config")
      if (config) {
        this.$store.dispatch("config/setUser", config.user)
        this.$store.dispatch("config/setBasicConfig", config.basicConfig)
        this.$store.dispatch("config/setTopicConfig", config.topicConfig)
        return
      }
      getBasicConfigApi().then(res => {
        //console.log(res)
        setConfig(res.data)
        let {user, basicConfig, topicConfig} = res.data
        this.$store.dispatch("config/setUser", user)
        this.$store.dispatch("config/setBasicConfig", basicConfig)
        this.$store.dispatch("config/setTopicConfig", topicConfig)
      })
    }
  },
}
</script>

<style scoped>
@import "https://cdns.jks.life/blog/content/logo.png";
@import "https://cdns.jks.life/blog/css/mdui.min.css";
@import "https://cdns.jks.life/blog/css/cuckoo.min.css";
@import "https://cdns.jks.life/blog/css/atom-one-dark.min.css";
@import "https://cdns.jks.life/blog/css/iconfont.min.css";
@import "https://cdns.jks.life/blog/css/fancybox.min.css";

.body{
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  scroll-behavior: smooth;
}
.container {
  min-height: 500px;
  width: 100%;
}

.box {
  padding: 10px;
}
</style>
