import request from '../utils/request'

export function login(loginDto){
    return request({
        url:"/ums/user/login",
        method:"post",
        data:loginDto
    })
}

export function getInfo(){
    return request({
        url:"/ums/user/info",
        method:"get"
    })
}

export function logout() {
    return request({
        url: '/ums/user/logout',
        method: 'get'
    })
}

export function updateAvatarApi(avatar){
    return request({
        url: '/ums/user/updateAvatar',
        method: 'put',
        data:{avatar:avatar}
    })
}
export function updatePasswordApi(password){
    return request({
        url: '/ums/user/updatePassword',
        method: 'put',
        data:password
    })
}
export function updateApi(data){
    return request({
        url: '/ums/user/update',
        method: 'put',
        data
    })
}
export function getPasswordApi(){
    return request({
        url: '/ums/user/getPassword',
        method: 'get'
    })
}
