<template>
  <div class="mdui-card mdui-hoverable sidebar-module">
    <ul class="mdui-list">
      <div class="sidebar-module-title"></div>
      <li class="mdui-divider mdui-m-y-0"></li>
      <div class="sidebar-tag">
        <router-link :to="{name:'tags',params:{name:item.name}}" v-for="(item,index) in tagsData" :key="index" data-pjax-state="">
          <div class="mdui-chip">
            <span class="mdui-chip-title">{{item.name}}</span>
          </div>
        </router-link>
      </div>
    </ul>
  </div>
</template>

<script>
import {getTagsAPi} from "../../api";

export default {
  name: "Tags",
  data() {
    return {
      tagsData:[]
    }
  },
  created() {
    this.getTags()
  },
  methods: {
    getTags() {
      getTagsAPi().then(res=>{
        this.tagsData=res.data
      })
    }
  },
}
</script>

<style scoped>
.mdui-chip{
  margin-right: 10px;
}
</style>
