<template>
  <header>
    <div
        class="mdui-appbar mdui-shadow-0  mdui-appbar-scroll-hide mdui-headroom mdui-headroom-pinned-top">
      <div class="mdui-toolbar">
        <a class="mdui-btn mdui-btn-icon" @click="show=true">
          <i class="mdui-icon material-icons">menu</i>
        </a>
        <router-link to="/" class="mdui-typo-title" data-pjax-state="">
          {{ basicConfig.siteName }}
        </router-link>
        <div class="mdui-toolbar-spacer"></div>
        <div class="mdui-textfield mdui-textfield-expandable mdui-float-right appbar-search"
        :class="{'mdui-textfield-expanded':showSearch}">
          <button class="mdui-textfield-icon mdui-btn mdui-btn-icon"
                  @click="showSearch=true;$refs.search.focus()">
            <i class="mdui-icon material-icons">search</i>
          </button>

            <input ref="search" class="mdui-textfield-input" type="text"
                   id="s" name="s" placeholder=""
            @keypress.enter="search" v-model="searchKey">

          <button class="mdui-textfield-close mdui-btn mdui-btn-icon" @click="showSearch=false">
            <i class="mdui-icon material-icons">close</i>
          </button>
        </div>
        <el-popover
            placement="top"
            width="160"
            trigger="hover">
          <el-image :src="src"></el-image>
          <button slot="reference" class="mdui-btn mdui-btn-icon qrcode"
                  style="margin-right: 20px"
                  :style="$route.name==='post'?{display:'block'}:{position:'fixed'}">
            <i class="mdui-icon material-icons">devices</i>
          </button>
        </el-popover>
      </div>
    </div>
    <el-drawer
        direction="ltr"
        :visible.sync="show"
        :with-header="false"
        :size="bodyWidth>800?bodyWidth*0.3+'px':bodyWidth*0.6+'px'">
      <div class="drawer-img">
        <img class="mdui-img-circle"
             :src="userConfig.avatar"
             alt="">
      </div>
      <div class="drawer-contact">
        <a target="_blank" :href="item.socialLink" v-for="(item,index) in topicConfig.socials"
        :key="index">
        <button class="mdui-btn mdui-btn-icon mdui-ripple">
          <i class="fa" :class="item.socialIcon"></i></button>
      </a>
      </div>
      <div class="mdui-divider"></div>
      <ul class="mdui-list drawer-list">
        <router-link to="/" data-pjax-state="">
          <li class="mdui-list-item mdui-ripple"><i class="mdui-list-item-icon mdui-icon material-icons">home</i>
            <div class="mdui-list-item-content"></div>
          </li>
        </router-link>
        <li class="mdui-collapse-item" @click="showCategory=!showCategory"
            :class="{'mdui-collapse-item-open':showCategory}">
          <div class="mdui-collapse-item-header mdui-list-item mdui-ripple">
            <i class="mdui-icon material-icons mdui-list-item-icon">view_list</i>
            <div class="mdui-list-item-content"></div>
            <i class="mdui-icon material-icons mdui-list-item-icon mdui-collapse-item-arrow">keyboard_arrow_down</i>
          </div>
          <el-collapse-transition>
            <ul v-show="showCategory" class="mdui-collapse-item-body mdui-list mdui-list-dense">
              <router-link :to="{name:'categories',params:{name: item.id}}"
                 v-for="(item,index) in category" :key="index"
                 class="mdui-list-item mdui-ripple"
                 data-pjax-state="">
                <div class="mdui-list-item-content">{{ item.name }}</div>
                <div class="drawer-item">{{ item.count }}</div>
              </router-link>
            </ul>
          </el-collapse-transition>

        </li>
        <li class="mdui-collapse-item" @click="showArchive=!showArchive"
            :class="{'mdui-collapse-item-open':showArchive}">
          <div class="mdui-collapse-item-header mdui-list-item mdui-ripple">
            <i class="mdui-icon material-icons mdui-list-item-icon">access_time</i>
            <div class="mdui-list-item-content"></div>
            <i class="mdui-icon material-icons mdui-list-item-icon mdui-collapse-item-arrow">
              keyboard_arrow_down
            </i>
          </div>
          <ul class="mdui-collapse-item-body mdui-list mdui-list-dense">
            <router-link :to="{name:'archive',params:{name:item.archive}}"
                         v-for="(item,index) in archive" :key="index"
                         class="mdui-list-item mdui-ripple"
               data-pjax-state="">
              <div class="mdui-list-item-content">{{item.archive}}</div>
              <div class="drawer-item">{{ item.count }}</div>
            </router-link>
          </ul>
        </li>
        <router-link :to="{name:'about'}" class="mdui-list-item mdui-ripple" data-pjax-state="">
          <i class="mdui-icon material-icons mdui-list-item-icon">face</i>
          <div class="mdui-list-item-content"></div>
        </router-link>
        <router-link :to="{name:'friends'}" class="mdui-list-item mdui-ripple"
           data-pjax-state="">
          <i class="mdui-icon material-icons mdui-list-item-icon">links</i>
          <div class="mdui-list-item-content"></div>
        </router-link></ul>
    </el-drawer>
  </header>
</template>

<script>

import {mapGetters} from 'vuex'
import {getHeaderApi} from "../../api";

export default {
  name: 'Header',
  data() {
    return {
      disabled:false,
      searchKey: '',
      darkMode: false,
      show: false,
      showCategory: false,
      showArchive: false,
      showSearch:false,
      category: [],
      archive: [],
      src:`https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=${location.href}`
    }
  },
  filters:{
    timeFormat(value){
      return this.dayjs(value).format("YYYYMMDD")
    }
  },
  computed: {
    ...mapGetters(['basicConfig', 'userConfig','topicConfig']),
    bodyWidth() {
      return document.getElementsByTagName("body")[0].clientWidth
    },
    path(){
      return this.$route.path
    }
  },
  watch: {
    path(){
      this.show=false
    }

  },
  mounted() {

  },
  created() {
    this.getHeader()
  },
  methods: {
    async logout() {
      this.$store.dispatch('user/logout').then(() => {
        this.$message.info('')
        setTimeout(() => {
          this.$router.push({path: this.redirect || '/'})
        }, 500)
      })
    },
    search() {
      //console.log(this.searchKey)
      if (this.searchKey.trim() === null || this.searchKey.trim() === '') {
        this.$message.info({
          showClose: true,
          message: '',
          type: 'warning'
        })
        return false
      }
      this.$router.push({name:'search',params:{name:this.searchKey}})
    },
    getHeader(){
      getHeaderApi().then(res=>{
        //console.log(res)
        let {archive,category} = res.data
        this.archive=archive
        this.category=category
      })
    }
  }
}
</script>

<style scoped>
.appbar-search {
  top: 0 !important;
}
</style>
