<template>
  <div class="mdui-card mdui-hoverable sidebar-info">
    <div class="sidebar-info-img">
      <div class="sidebar-info-bg"
           :style="`background-image: url(${topicConfig.avatarBackGround})`">
      </div>
      <div class="mdui-img-circle mdui-shadow-3"
           :style="`background-image: url(${userConfig.avatar})`">
      </div>
    </div>
    <div class="sidebar-info-body">
      <div class="sidebar-info-name">{{ userConfig.alias }}</div>
      <div class="sidebar-info-desc" :class="{'text':topicConfig.blogErJsPlus}"
           id="binft"
           v-html="basicConfig.siteDescription">
      </div>
      <div style="width: 100%; position: relative;" v-show="topicConfig.particleClock">
        <canvas id="canvasDiyBlock" ref="canvasDiyBlock" style="width:100%;">
          canvas
        </canvas>
      </div>
    </div>

  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import cool from "../../utils/magic/cool";
import clock from "../../utils/magic/clock";
export default {
  name: "Introduce",
  data() {
    return {
      backgroundImage: 'https://s.cn.bing.net/th?id=OHR.RomanView_ZH-CN6469652448_1920x1080.jpg&rf=LaDigue_1920x1080.jpg',
    }
  },
  computed: {
    ...mapGetters(['userConfig', 'basicConfig', 'topicConfig'])
  },
  created() {

  },
  mounted() {
   if (this.topicConfig.particleClock){
     clock()
   }
   if (this.topicConfig.blogErJsPlus){
     cool()
   }
  },
  methods: {},
}
</script>

<style scoped>
.mdui-img-circle {
  top: 55px !important;
}

.text {
  letter-spacing: 0.2rem;
  background-image: -webkit-linear-gradient(left,
  #ff0000,
  #ff5a00 10%,
  #ffe808 20%,
  #00ff14 30%,
  #00f8b1 40%,
  #032bff 50%,
  #a600ff 60%,
  #fa9d9d 70%,
  #00e3ff 80%,
  #8e8a4c 90%,
  #147B96);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-background-size: 200% 100%;
  -webkit-animation: maskedAnimation 6s infinite linear;
}

@keyframes maskedAnimation {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: -50% 0;
  }
  100% {
    background-position: -100% 0;
  }
}

.neon {
  color: #cce7f8;
  -webkit-animation: shining 0.5s alternate infinite;
  animation: shining 0.5s alternate infinite;
}

@-webkit-keyframes shining {
  from {
    text-shadow: 0 0 10px lightblue, 0 0 20px lightblue, 0 0 30px lightblue, 0 0 40px skyblue, 0 0 50px skyblue, 0 0 60px skyblue;
  }

  to {
    text-shadow: 0 0 5px lightblue, 0 0 10px lightblue, 0 0 15px lightblue, 0 0 20px skyblue, 0 0 25px skyblue, 0 0 30px skyblue;
  }
}

.typing {
  color: #000;
  width: 21em;
  height: 1.5em;
  border-right: 1px solid transparent;
  animation: typing 2s steps(42, end), blink-caret .75s step-end infinite;
  word-break: break-all;
  overflow: hidden;
}

/*  */
@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 21em;
  }
}

/*  */
@keyframes blink-caret {

  from,
  to {
    border-color: transparent;
  }

  50% {
    border-color: currentColor;
  }
}


.tex::before {
  content: attr(data-text);
  position: absolute;
  left: -2px;
  width: 100%;
  text-shadow: 2px 0 red;
  animation: animation-before 3s infinite linear alternate-reverse;
}

.tex::after {
  content: attr(data-text);
  position: absolute;
  left: 2px;
  width: 100%;
  text-shadow: -2px 0 blue;
  animation: animation-after 3s infinite linear alternate-reverse;
}

@keyframes animation-before {
  0% {
    clip-path: inset(0 0 0 0);
  }
  20% {
    clip-path: inset(.12em 0 .06em 0);
  }
  40% {
    clip-path: inset(.24em 0 .12em 0);
  }
  60% {
    clip-path: inset(.36em 0 .18em 0);
  }
  80% {
    clip-path: inset(.48em 0 .24em 0);
  }
  100% {
    clip-path: inset(.62em 0 .29em 0);
  }
}

@keyframes animation-after {
  0% {
    clip-path: inset(0 0 0 0);
  }
  20% {
    clip-path: inset(.06em 0 .12em 0);
  }
  40% {
    clip-path: inset(.12em 0 .24em 0);
  }
  60% {
    clip-path: inset(.18em 0 .36em 0);
  }
  80% {
    clip-path: inset(.24em 0 .48em 0);
  }
  100% {
    clip-path: inset(.29em 0 .62em 0);
  }
}
</style>
